<template>
  <v-carousel class="v-carousel-main" cycle :show-arrows="false" height="100%" vertical-delimiters>
    <v-carousel-item
      v-for="(item, i) in SLIDES.main"
      :key="i"
      :src="require('../../assets/images/' + item.url)"
    >
      <div class="info" v-html="'<p>' + item.title + '</p>'"></div>
    </v-carousel-item>
  </v-carousel>
</template>

<script>
import { mapActions, mapGetters } from "vuex";

export default {
  name: "v-carousel-main",
  data() {
    return {};
  },
  methods: {
    ...mapActions(["GET_SLIDES_FROM_API"]),
  },
  mounted() {
    this.GET_SLIDES_FROM_API();
  },
  computed: {
    ...mapGetters(["SLIDES"]),
  },
};
</script>

<style lang="scss" scoped>
.v-carousel-main {
  width: 100%;
  max-height: 100vh;
}
.info {
  position: absolute;
  left: 50px;
  bottom: 25%;
  font-family: $fontSecondary;
  font-size: calc(20px + 30 * ((100vw - 320px) / (1280 - 320)));
  text-shadow: $textshadow;
  color: #fff;
}
</style>
