import Vue from "vue";
import Vuex from "vuex";
import Axios from "axios";
import common from "./modules/common";
import * as fb from "firebase/app";

Vue.use(Vuex);

let store = new Vuex.Store({
  state: {
    products: [],
    slider: {},
    category: [],
    contacts: {},
    trand: {},
    deals: {},
    newProducts: [],
    isVisibleModalCall: false,
    cart: [],
    isModalProduct: false,
    oneProduct: {},
  },
  mutations: {
    SET_PRODUCTS_TO_STATE: (state, products) => {
      state.products = products;
    },
    SET_CONTACTS_TO_STATE: (state, contacts) => {
      state.contacts = contacts;
    },
    SET_SLIDES_TO_STATE: (state, slides) => {
      state.slider = slides;
    },
    SET_TRAND_TO_STATE: (state, trand) => {
      state.trand = trand;
    },
    SET_DEALS_TO_STATE: (state, deals) => {
      state.deals = deals;
    },
    SET_NEW_TO_STATE: (state, newProducts) => {
      state.newProducts = newProducts;
    },
    SET_CATEGORY_TO_STATE: (state, category) => {
      state.category = category;
    },
    CHANGE_MODAL_CALL: (state) => {
      state.isVisibleModalCall = !state.isVisibleModalCall;
    },
    SET_CART: (state, product) => {
      if (state.cart.length) {
        let isProductExists = false;
        state.cart.map((item) => {
          if (item.id === product.id) {
            isProductExists = true;
            item.quantity++;
          }
        });

        if (!isProductExists) {
          state.cart.push(product);
        }
      } else {
        state.cart.push(product);
      }
    },
    REMOVE_FROM_CART: (state, index) => {
      state.cart.splice(index, 1);
    },
    CHANGE_MODAL_PRODUCT: (state, product) => {
      state.isModalProduct = !state.isModalProduct;
      if (product) {
        state.oneProduct = product;
      }
    },
  },
  actions: {
    async GET_PRODUCTS_FROM_API({ commit }) {
      commit("CLEAR_ERROR");
      commit("SET_LOADING", true);
      try {
        const productsVal = await fb
          .database()
          .ref("products")
          .once("value");
        const products = productsVal.val();
        commit("SET_PRODUCTS_TO_STATE", products);
        commit("SET_LOADING", false);
        return products;
      } catch (error) {
        commit("SET_ERROR", error.message);
        commit("SET_LOADING", false);
        throw error;
      }
    },
    async GET_CONTACTS_FROM_API({ commit }) {
      commit("CLEAR_ERROR");
      commit("SET_LOADING", true);
      try {
        const contactsVal = await fb
          .database()
          .ref("contacts")
          .once("value");
        const contacts = contactsVal.val();
        commit("SET_CONTACTS_TO_STATE", contacts);
        commit("SET_LOADING", false);
        return contacts;
      } catch (error) {
        commit("SET_ERROR", error.message);
        commit("SET_LOADING", false);
        throw error;
      }
    },
    async GET_SLIDES_FROM_API({ commit }) {
      commit("CLEAR_ERROR");
      commit("SET_LOADING", true);
      try {
        const slidesVal = await fb
          .database()
          .ref("slider")
          .once("value");
        const slides = slidesVal.val();
        commit("SET_SLIDES_TO_STATE", slides);
        commit("SET_LOADING", false);
        return slides;
      } catch (error) {
        commit("SET_ERROR", error.message);
        commit("SET_LOADING", false);
        throw error;
      }
    },
    async GET_TRAND_FROM_API({ commit }) {
      commit("CLEAR_ERROR");
      commit("SET_LOADING", true);
      try {
        const trandVal = await fb
          .database()
          .ref("trand")
          .once("value");
        const trand = trandVal.val();
        commit("SET_TRAND_TO_STATE", trand);
        commit("SET_LOADING", false);
        return trand;
      } catch (error) {
        commit("SET_ERROR", error.message);
        commit("SET_LOADING", false);
        throw error;
      }
    },
    async GET_DEALS_FROM_API({ commit }) {
      commit("CLEAR_ERROR");
      commit("SET_LOADING", true);
      try {
        const dealsVal = await fb
          .database()
          .ref("deals")
          .once("value");
        const deals = dealsVal.val();
        commit("SET_DEALS_TO_STATE", deals);
        commit("SET_LOADING", false);
        return deals;
      } catch (error) {
        commit("SET_ERROR", error.message);
        commit("SET_LOADING", false);
        throw error;
      }
    },
    async GET_NEW_FROM_API({ commit }) {
      commit("CLEAR_ERROR");
      commit("SET_LOADING", true);
      try {
        const newProductsVal = await fb
          .database()
          .ref("new")
          .once("value");
        const newProducts = newProductsVal.val();
        commit("SET_NEW_TO_STATE", newProducts);
        commit("SET_LOADING", false);
        return newProducts;
      } catch (error) {
        commit("SET_ERROR", error.message);
        commit("SET_LOADING", false);
        throw error;
      }
    },
    async GET_CATEGORY_FROM_API({ commit }) {
      commit("CLEAR_ERROR");
      commit("SET_LOADING", true);
      try {
        const categoryVal = await fb
          .database()
          .ref("category")
          .once("value");
        const category = categoryVal.val();
        commit("SET_CATEGORY_TO_STATE", category);
        commit("SET_LOADING", false);
        return category;
      } catch (error) {
        commit("SET_ERROR", error.message);
        commit("SET_LOADING", false);
        throw error;
      }
    },
    ACTIVE_MODAL_CALL({ commit }) {
      commit("CHANGE_MODAL_CALL");
    },
    ADD_TO_CART({ commit }, product) {
      commit("SET_CART", product);
    },
    DELETE_PRODUCT_FROM_CART({ commit }, index) {
      commit("REMOVE_FROM_CART", index);
    },
    ACTIVE_MODAL_PRODUCT({ commit }, product) {
      commit("CHANGE_MODAL_PRODUCT", product);
    },
  },
  getters: {
    CONTACTS(state) {
      return state.contacts;
    },
    PRODUCTS(state) {
      return state.products;
    },
    SLIDES(state) {
      return state.slider;
    },
    TRAND(state) {
      return state.trand;
    },
    DEALS(state) {
      return state.deals;
    },
    NEWPRODUCTS(state) {
      return state.newProducts;
    },
    CATEGORY(state) {
      return state.category;
    },
    MODALCALL(state) {
      return state.isVisibleModalCall;
    },
    CART(state) {
      return state.cart;
    },
    MODALPRODUCT(state) {
      return state.isModalProduct;
    },
    ONEPRODUCT(state) {
      return state.oneProduct;
    },
  },
  modules: {
    common,
  },
});

export default store;
