<template>
  <div class="v-select">
    <p class="title" @click="areOptionsVisible = !areOptionsVisible" v-text="selected">Select</p>
    <div class="options" v-if="areOptionsVisible">
      <span
        v-for="option in options"
        :key="option.value"
        @click="selectOption(option)"
      >{{option.name}}</span>
    </div>
  </div>
</template>

<script>
export default {
  name: "v-select",
  props: {
    options: {
      type: Array,
      default() {
        return [];
      },
    },
    selected: {
      type: String,
      default() {
        return "";
      },
    },
  },
  data() {
    return {
      areOptionsVisible: false,
    };
  },
  methods: {
    selectOption(option) {
      this.$emit("select", option);
      this.areOptionsVisible = false;
    },
    hideSelect() {
      this.areOptionsVisible = false;
    },
  },
  mounted() {
    document.addEventListener("click", this.hideSelect.bind(this), true);
  },
  beforeDestroy() {
    document.addEventListener("click", this.hideSelect);
  },
};
</script>

<style lang="scss" scoped>
.v-select {
  position: relative;
  width: 250px;
  cursor: pointer;
}
.title {
  border: 1px solid #fff;
  padding: 5px;
  font-size: $fontSizeBase + 4;
  font-family: $fontBase;
  text-align: center;
  line-height: 30px;
  background-color: #000;
  color: $colorBase;
  cursor: pointer;
}
.options {
  text-align: center;
  border: 1px solid #fff;
  position: absolute;
  top: 0;
  right: 0;
  width: 100%;
  display: flex;
  flex-direction: column;
  background-color: #000;
  color: #fff;

  span {
    padding: 5px;
    font-size: $fontSizeBase + 4;
    font-family: $fontBase;
    text-align: center;
    line-height: 30px;
    background-color: #000;
    color: $colorBase;

    &:hover {
      background-color: lighten(#000, 20);
    }
  }
}
</style>