<template>
  <div id="App">
    <v-header @toggleMenu="toggleMenu" />
    <keep-alive>
      <router-view></router-view>
    </keep-alive>
    <v-footer />
    <v-modal-menu
      :visible="isModalMenuVisible"
      @visible="isModalMenuVisible = !isModalMenuVisible"
    />
    <v-modal-call />
    <v-modal-age />
    <v-modal-about />
    <template v-if="error">
      <v-snackbar
        :multi-line="true"
        :timeout="5000"
        color="error"
        :value="true"
        @input="closeError"
      >
        {{ error }}
        <v-btn dark flat v-bind="attrs" @click.native="closeError">Close</v-btn>
      </v-snackbar>
    </template>
  </div>
</template>

<script>
import vHeader from "./components/v-header";
import vFooter from "./components/v-footer";
import vModalMenu from "./components/modal/v-modal-menu";
import vModalCall from "./components/modal/v-modal-call";
import vModalAge from "./components/modal/v-modal-age";
import vModalAbout from "./components/modal/v-modal-about";

export default {
  name: "App",

  components: {
    vHeader,
    vFooter,
    vModalMenu,
    vModalCall,
    vModalAge,
    vModalAbout,
  },
  data() {
    return {
      isModalMenuVisible: false,
    };
  },
  methods: {
    toggleMenu() {
      this.isModalMenuVisible = !this.isModalMenuVisible;
    },
    closeError() {
      this.$store.dispatch("clearError");
    },
  },
  computed: {
    error() {
      return this.$store.getters.error;
    },
  },
};
</script>
