<template>
  <div class="v-modal-checkout" v-if="isCheckout">
    <div class="v-modal-checkout__block">
      <div class="v-modal-checkout__block-name">Оформление заказа</div>
      <div class="v-modal-checkout__block-close" @click="checkOutClose">
        <i class="fas fa-times"></i>
      </div>
    </div>

    <div class="v-modal-checkout__block-container">
      <v-select />
    </div>
  </div>
</template>

<script>
import vSelect from "../v-select";

export default {
  name: "v-modal-checkout",
  components: {
    vSelect,
  },
  props: {
    isCheckout: {
      type: Boolean,
      default() {
        return false;
      },
    },
  },
  methods: {
    checkOutClose() {
      this.$emit("checkOutClose");
    },
  },
};
</script>

<style lang="scss">
.v-modal-checkout {
  width: 100%;
  height: 100vh;
  position: fixed;
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 99;
  padding: $padding;
  top: 0;
  left: 0;

  &__block {
    max-width: 800px;
    width: 100%;
    height: 500px;
    background-color: #fff;
    padding: $padding;
    position: relative;

    &-name {
      font-family: $fontBase;
      font-size: calc(12px + 5 * ((100vw - 320px) / (1280 - 320)));
      padding: 0 0 $padding 0;
      border-bottom: 1px solid $colorBorder;
    }
    &-close {
      position: absolute;
      width: 30px;
      height: 30px;
      background-color: #fff;
      top: 0;
      right: 0;
      color: #ccc;
      font-size: $fontSizeBase + 6;
      text-align: center;
      line-height: 30px;
      border: 2px solid #ccc;
      cursor: pointer;
    }
  }
}
</style>